import { StylePrototypes } from './common';
export const Link = {
    name: 'Link',
    title: '',
    exportType: 'namedExport',
    icon: 'icon-link-button',
    type: 'element',
    package: '@music163/antd',
    help: '',
    hasChildren: true,
    props: [
        ...StylePrototypes,
        {
            name: 'children',
            title: '',
            setter: 'textSetter',
            initValue: '',
        },
        {
            name: 'disabled',
            title: '',
            setter: 'boolSetter',
        },
        {
            name: 'to',
            title: '',
            tip: ' http  http ',
            setter: 'textSetter',
        },
        {
            name: 'params',
            title: '',
            tip: 'url ',
            setter: 'enumSetter',
        },
        {
            name: 'target',
            title: '',
            setter: 'choiceSetter',
            options: [
                { label: '', value: '_blank' },
                { label: '', value: '_self' },
            ],
            getVisible: (form) => {
                const to = form.getValue('to') || '';
                // 
                return to.replace(/https?:/, '').startsWith('//');
            },
        },
    ],
};
